<template>
  <section class="reviewOrder">
    <div class="container">
      <div v-if="!itemsInCart > 0">
        <h2 class="mt-5 alert alert-danger">No Orders Added Yet.</h2>
        <router-link to="/">
          <button class="btn btn-success">Start Shopping</button>
        </router-link>
      </div>
      <div v-else>
        <h2 class="text-center my-5">Review Order</h2>
        <table class="table">
          <thead>
            <tr>
              <th>#</th>
              <th>Item</th>
              <th>Quantity</th>
              <th>Price/item</th>
              <th>Total Price</th>
            </tr>
          </thead>
          <tbody v-for="(item, index) in inCart" :key="index">
            <tr>
              <td scope="row">{{ index + 1 }}</td>
              <td>{{ item.name }}</td>
              <td>
                <input
                  type="number"
                  v-model.number="item.qty"
                  placeholder="Quantity"
                  min="0"
                />
              </td>
              <td>{{ item.price * quantity }}</td>
              <td>{{ (total += item.price * quantity) }}</td>
            </tr>
          </tbody>
        </table>
        <router-link to="/FormOrder">
          <button class="btn btn-success">Order Now</button>
        </router-link>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "ReviewOrder",
  computed: {
    inCart() {
      return this.$store.getters.inCart;
    },
    itemsInCart() {
      return this.inCart.length;
    }
  },
  data() {
    return {
      quantity: 1,
      total: 0
    };
  }
};
</script>
